  font-size: 2.8rem;
  margin-bottom: 2rem;
  margin-right: 0.3rem;
}

* {
  font-family: "Amiri", serif;
  border: none !important;
  box-sizing: border-box;
  padding: 0;
  border-radius: 0;
  text-align: right;
  transition: 0.5s;
  scroll-behavior: smooth;
}

.row {
  margin: 0;
}

.navbar img {
